import {Component, OnInit} from '@angular/core';
import {IBreadcrumb} from '../../shared/breadcrumb/breadcrumb.component';
import {ActivatedRoute} from '@angular/router';
import 'movida-regularize-payment';
import {CheckLoginService} from '../../core/services/checklogin.service';
import {AlertMessageComponent} from '../../shared/alert-message/alert-message.component';

@Component({
  selector: 'app-regularizar-pagamentos',
  templateUrl: './regularizar-pagamentos.component.html',
  styleUrls: ['./regularizar-pagamentos.component.scss']
})

export class RegularizarPagamentosComponent implements OnInit {

  public steps: IBreadcrumb[] = [
    {
      id: '0',
      label: 'Home',
      link: '/'
    },
    {
      id: '1',
      label: 'Meu portal',
      link: '/portal'
    },
    {
      id: '2',
      label: 'Meus pagamentos',
      link: '/portal/meus-pagamentos'
    },
    {
      id: '3',
      label: 'Regularizar pagamento'
    }
  ];
  public regularizeKey: string;
  public user: any;
  public userToken: any;
  protected checkLoginInterval: any;

  constructor(
    public route: ActivatedRoute,
    private checkLoginService: CheckLoginService,
    public alertMsg: AlertMessageComponent
  ) {
    this.user = JSON.parse(localStorage.getItem('_user'));
    this.userToken = this.user.access_token;
    this.checkLoginInterval = null;
  }

  ngOnInit() {
    this.checkLoginService.check();
    this.regularizeKey = this.route.snapshot.params.regularizeKey;
  }

  checkLogin() {
    if (this.checkLoginInterval !== null) {
      clearInterval(this.checkLoginInterval);
    }
    this.checkLoginInterval = setInterval(() => {
      if (this.userToken === null) {
        this.alertMsg.openDialog({
          tipo: 'message',
          titulo: 'Atenção!',
          mensagem: 'Sua sessão expirou!'
        }, 'message', true);
      }
    }, 60000);
  }

}
